@import (reference) "~assets/css/common.less";

.layout-wrapper { 
    display: flex; 
    flex-flow: column nowrap; 
    height: 100%; 
    overflow: hidden; 
    background-color: @color-bg-base; 
    > div:first-child { 
        height: 0.7rem; 
        flex-basis: 0.7rem; 
        flex-shrink: 0; 
    } 
    .layout-wrapper_body { 
        min-height: 0; 
        flex: 1 1 0; 
        display: flex; 
        flex-flow: row nowrap; 
        position: relative; 
        > div { 
            height: 100%; 
            overflow-y: auto; 
            overflow-x: hidden; 
        } 
        .base_container { 
            z-index: 1; 
            flex-shrink: 0; 
            box-shadow: 0.02rem 0 0.05rem 0 rgba(0, 0, 0, 0.1); 
            background-color: @color-menu-default; 
        } 
    } .layout-wrapper_content { 
        display: flex; 
        position: relative; 
        flex: 1; 
        background-color: @color-bg-base; 
        // padding: 0.2rem; 
    } 
}