@import "../fonts/iconfont/iconfont.css";

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr,
acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, 
strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, 
legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, 
embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, 
time, mark, audio, video { 
    margin: 0; 
    padding: 0; 
    border: 0; 
    font-size: 100%; 
    font-family: "PingFangSC", "Microsoft YaHei UI", sans-serif !important; 
    vertical-align: baseline; 
    box-sizing: border-box; 
} /* HTML5 display-role reset for older browsers */ 
a { 
    color: #000000 
} 
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { 
    display: block; 
} 
body { 
    line-height: 1; 
} 
ol, ul { 
    list-style: none; 
    margin-bottom: 0; 
}
html ,body ,#root {
    height: 100%;
    width: 100%;
    font-size: .16rem;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
input,button,input:focus,button:focus{
    font-family: "Microsoft YaHei UI", sans-serif !important;
    outline: none;
}

/* 设置文本框 placeholder 颜色 */
input::placeholder {
    color: #999 !important;
}
input::-webkit-input-placeholder{
    color: #999 !important;
}
:-moz-placeholder{
    color: #999 !important;
}
::-moz-placeholder{
    color: #999 !important;
}
input:-ms-input-placeholder{
    color: #999 !important;
}
input::-ms-input-placeholder{
    color: #999 !important;
}
textarea::-webkit-input-placeholder{
    color: #999 !important;
}
textarea:-ms-input-placeholder{
    color: #999 !important;
}
textarea::-ms-input-placeholder{
    color: #999 !important;
}
/* 解决 placeholder 文本不垂直居中的问题*/
input::placeholder{
    line-height: normal;
}
input::-webkit-input-placeholder{
    line-height: normal;
}
input::-moz-placeholder{
    line-height: normal;
}
input:-moz-placeholder{
    line-height: normal;
}
input:-ms-input-placeholder{
    line-height: normal;
}
/* 滚动条的整体 */
::-webkit-scrollbar
{
    width: 8px;
    height: 8px;
    background-color: transparent;
    border-radius: 5px;
    overflow: visible;
}
/* 滚动条的轨道 */
::-webkit-scrollbar-track
{
    -webkit-box-shadow: none;
    background: transparent;
    border-radius: 5px;
    width: 6px;
}
/* 滚动条的滑块 */
::-webkit-scrollbar-thumb
{
    border-radius: 5px;
    -webkit-box-shadow: none;
    /* background-color: transparent; */
    background: linear-gradient(90deg,transparent 0,transparent 1px,rgba(151,168,190,.3) 0,rgba(151,168,190,.3) 7px,transparent 0,transparent 8px)!important;
    border: 0 none #fff;
    display: none;
}
/* 滚动条的hover状态 */
*:hover>::-webkit-scrollbar{
    /* background-color:rgba(255,255,255,0.2); */
}
*:hover>::-webkit-scrollbar-track{
    /* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0); */
    /* background: #d5dce8; */
}
*:hover>::-webkit-scrollbar-thumb{
    display: block;
}
*:hover>::-webkit-scrollbar-thumb:hover{
    background: rgba(151,168,190,.5)!important;
}