@import (reference) "~assets/css/common.less";

@menu-item-height: 0.55rem; 
@active-bg-color: #ebf0ff; 
@padding: .2rem; 

.menu-widget-container{ 
    position: relative; 
    z-index: 9999; 
    overflow-y: auto; 
    .menu-active{ 
        color: @color-theme!important; 
        font-weight: bolder!important; 
    } 
    .parent-wrapper{ 
        position: relative; 
        width: 100%; 
        display: flex; 
        justify-content: flex-start; 
        align-items: center; 
        font-size: 0.16rem; 
        transition: all .3s; 
        overflow: hidden; 
        cursor: pointer; 
        user-select: none; 
        &.menu-active{ 
            background-color: #fff; 
            &:before{ 
                content: ""; 
                position: absolute; 
                z-index: 1; 
                top: 0; 
                left: 0; 
                width: 0.03rem; 
                height: @menu-item-height; 
                background-color: @color-theme; 
            } 
        } 
        .icon { 
            width: 0.2rem; 
            height: 0.2rem; 
            font-size: .18rem; 
        } 
        .arrow-up,.arrow-down{ 
            position: absolute; 
            right: .2rem; 
            opacity: 1; 
            transition: transform .3s; 
            transform: rotate(90deg); 
        } 
        .arrow-up{ 
            transform: rotate(-90deg); 
        }
        .menu-item{ 
            position: relative; 
            width: 100%; 
            padding: 0 @padding 0 @padding; 
            min-width: 1rem; 
            color: @color333; 
            font-weight: normal; 
            display: flex; 
            justify-content: space-between; 
            align-items: center; 
            padding-right: @padding; 
            cursor: pointer; 
            .menu-item-title{ 
                height: @menu-item-height; 
                overflow: hidden; 
                white-space: nowrap; 
                padding: 0 .15rem; 
                display: inline-flex; 
                align-items: center; 
                transition: all .3s; 
            } 
            .iconfont{ 
                font-size: .12rem; 
                color: #9a9a9a; 
            } 
            &:hover { 
                background-color: @active-bg-color; 
                color: @color-theme; 
                &>.children-wrapper{ 
                    display: block; 
                } 
            } 
        } 
        &>.menu-item{ 
            justify-content: start; 
        } 
    } 
    .children-wrapper{ 
        width: 100%; 
        .children-wrapper{ 
            display: none; 
            box-shadow: 0.02rem 0.02rem 0.05rem 0 rgba(0, 0, 0, 0.1); 
            position: fixed; 
            background-color: #fff; 
            width: 2rem; 
            top: 0; 
        } 
    } 
}
.expand-menu-container{ 
    .parent-wrapper{ 
        flex-wrap: wrap; 
        &>.children-wrapper{ 
            &>.menu-item{ 
                padding-left: .4rem; 
            } 
        } 
    } 
} 
.collapse-menu-container{ 
    .parent-wrapper{ 
        height: @menu-item-height!important; 
        &:hover { 
            background-color: @active-bg-color!important; 
            color: @color-theme; 
        } 
        .parent-name,.arrow-down,.arrow-up{ 
            display: none!important; 
        } 
        &:hover { 
            &>.children-wrapper{ 
                display: block; 
            } 
        } 
    } 
    .children-wrapper{ 
        display: none; 
        position: absolute; 
        background-color: #fff; 
        box-shadow: 0.02rem 0.02rem 0.05rem 0 rgba(0, 0, 0, 0.1); 
        width: 2rem; 
        left: 100%; 
        top: 0; 
    } 
}