.base_container { 
    position: relative; 
    height: 100%; 
    padding-bottom: 0.7rem; 
    transition: all 0.3s linear; 
    background: #fafafc; 
    padding-top: 0.1rem; 
    box-shadow: 0.02rem 0 0.05rem rgba(200, 200, 200, 0.2); 
    > div { 
        height: 100%; 
        // border-top: 1px solid #e6e9f0; 
    } 
    .collaspeBtn { 
        position: absolute; 
        left: 0.2rem; 
        bottom: 0.4rem; 
        width: auto; 
        height: .25rem; 
        white-space: nowrap; 
        cursor: pointer; 
        border: none; 
        z-index: 999; 
        display: flex; 
        align-items: center; 
        color: #999; 
        .iconc-hidden { 
            font-size: 0.16rem; 
            margin-right: 0.2rem; 
            vertical-align: middle; 
            transition: all .5s; 
        } 
        .iconc-hidden.rotate-icon{ 
            transform: rotate(180deg); 
        } 
        .hidden-text { 
            font-size: 0.16rem; 
        } 
    } 
}