.header-container { 
    height: .7rem; 
    display: flex; 
    align-items: center; 
    justify-content: space-between; 
    padding: 0 .2rem; 
    font-size: .24rem; 
    color: #FFFFFF; 
    background: #427AFF; 
    .hb-title { 
        position: relative; 
        .logo { 
            width: .36rem; 
            height: .36rem; 
            margin-right: .2rem; 
            position: absolute; 
            left: 0; 
            top: 0; 
            bottom: 0; 
            margin: auto; 
        } 
        .system-name { 
            margin-left: .5rem; 
            display: flex; 
        } 
    } 
    .hb-system { 
        .iconfont { 
            color: #ffffff; 
        } 
        display: flex; 
        align-items: center; 
        .avatar { 
            margin-right: .1rem; 
            font-size: .23rem; 
            // background: url("../../../assets/image/uesr.png") no-repeat center center; 
            background-size: contain; 
            width: .23rem; 
            height: .23rem; 
        } 
        .user-name { 
            font-size: .18rem; 
            color: #FFFFFF; 
            white-space: nowrap; 
        } 
        .center-line { 
            width: .01rem; 
            height: .24rem; 
            background: rgba(255,255,255,.5); 
            margin: 0 .2rem; 
            display: inline-block; 
        } 
        .platform { 
            margin-right: .22rem; 
            cursor: pointer; 
        } 
        .back { 
            cursor: pointer; 
        } 
        i { 
            font-size: .2rem; 
        } 
    } 
}
