@color-theme: #427AFF;  //主色、高亮色（文字等）

@color-bg-base: #F5F6FA; // 用于主体背景、分页背景色
@color-menu-default: #FAFAFC; // 用于菜单默认情况下的背景
@color-table-header: #E5EBFB; // 用于列表表头背景
@color-table-devide: #F0F4FD; // 用于列表分隔背景
@color-table-hover: #DFE8FF; // 用于hover背景
@color-line: #E1E3EC; // 用于分割线  border-color


@color-succ: #7CCD3C; // 绿色      用于成功与正常情况
@color-warn: #FA7E03; // 橙色      用于强调与提醒情况
@color-error: #E82821; // 红色      用于警示与突出提醒情况

@color333: #333333; 
@color666: #666666; 
@color999: #999999; 
@colorccc: #CCCCCC; 
@colorfff: #FFFFFF;

@color-reayOnly: #F0F0F0;
@color-hover-bg: #DFE8FF;
@icon-default-color: #999999;  
@icon-hover-color: #427AFF;  
@color-yellow-color:#E6BF0F;

@position-left: .2rem;
@position-right: .4rem;
@position-bottom: .4rem;
@position-top: .2rem;
@container-background: rgba(255, 255, 255, .9);

@fontsize14: .14rem; 
@fontsize-theme: .16rem; 
@fontSize18: .18rem; 
@fontSize20: .20rem; 
@fontSize22: .22rem;
@fontSize24: .24rem;
@fontSize26: .26rem; 
@fontSize100: 1rem;

.ellipsis {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}