@import (reference) "~assets/css/common.less";

.nav-tool-container{ 
    display: flex; 
    flex-direction: column; 
    width: 100%; 
    height: 100%; 
    position: relative; 
    background: @color-bg-base; 

    .nav-tool-bar{ 
        background-color: #fff; 
        position: relative; 
        overflow: hidden; 
        .nav-tool-menutabs{ 
            font-size: 0; 
            width: calc(100% - .6rem); 
            .ellipsis; 
            .menu-tab{ 
                display: inline-block; 
                position: relative; 
                font-size: @fontsize14; 
                line-height: 2.5; 
                color: @color999; 
                margin: 0; 
                padding: 0 0.1rem 0 0.2rem; 
                border-radius: 0; 
                background: #fff; 
                cursor: pointer; 
                &:before{ 
                    content: ""; 
                    position: absolute; 
                    left: 0; 
                    top: 0; 
                    width: 100%; 
                    height: 100%; 
                    background: #fff; 
                    border-right: .01rem solid @color-line; 
                } 
                &>span{ 
                    position: relative; 
                    top: 0; 
                    z-index: 2; 
                } 
                .iconfont{ 
                    color: @color999; 
                    font-size: @fontsize14; 
                } 
                &:hover, .iconfont:hover{ 
                    color: @color-theme; 
                }
                &.menu-tab-active{
                        color:@color-theme;
                        font-weight: 600;
                        background-color: @color-bg-base;
                    .iconc-home1{
                        color:@color-theme;
                    }
                    &:before{
                        background-color: @color-bg-base;
                    }
                }
                &:first-of-type{
                        padding: 0 .2rem;
                    >div {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                }
                .iconc-home1:before{
                    transform: scale(1.2857);
                    display: block;
                }

                // 隐藏默认关闭图标
                .anticon-close{
                    display: none;
                }
                .btn-close{
                        margin-left: 0.2rem;
                    &:before{
                        display: inline-block;
                        transform: scale(0.7);
                    }
                }
            }
            .ant-tabs-tab-active{
                color: @color-theme;
                background: @color-bg-base;
            }
        }
        .btn-more{ 
            position: absolute; 
            color: @color999; 
            right: 0.2rem; top: 50%; 
            width: 0.18rem; 
            height: 0.18rem; 
            font-size: 0.18rem; 
            cursor: pointer; 
            z-index: 1; 
            display: flex; 
            align-items: center; 
            transform: translateY(-50%); 
        } 
    } 
    .nav-tool-content{ 
        flex: 1; 
        overflow: auto; 
        padding: .2rem .2rem; 
        position: relative; 
    } 
} 
.nav-tool-box-dropdown{ 
    font-size: @fontsize14; 
    .nav-tool-box{ 
        text-align: left; 
        padding: 0; 
        box-shadow: .02rem .02rem .05rem 0 rgba(0,0,0,0.10); 
        border-radius: 1px; 
        max-height: 4.5rem; 
        overflow: auto; 
        margin-top: calc((.4rem - 0.18rem) / 2); 
        background-color: #fff;
    } 
    .toolbox-dropdown-item{ 
        font-size: @fontsize14; 
        color: @color666; 
        padding: 0 0.26rem 0 0.2rem; 
        line-height: 2.5; 
        max-width: 2rem; 
        overflow-x: hidden; 
        white-space: nowrap; 
        text-overflow: ellipsis; 
        text-align: left; 
        cursor: pointer; 
    } .toolbox-dropdown-item--active{ 
        color: @color-theme; 
        font-weight: 600; 
    }
    /* 分割线 */ 
    .toolbox-dropdown-divider{
        border-bottom: .01rem solid @color-line;
    }

    .toolbox-dropdown-item:hover,
    .toolbox-dropdown-item:hover{
        background-color: @color-table-hover;
    }
}